import Vue from "vue";
import Router from "vue-router";

import Explore from "../components/Explore";
import ExploreFund from "../components/ExploreFund";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/explore"
    },
    {
      path: "/explore",
      name: "explore",
      component: Explore
    },
    {
      path: "/explore/:code",
      name: "exploreFund",
      component: ExploreFund
    }
  ]
});
