<template>
  <div class="text-center" v-if="loading">
    <div class="spinner-border" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>
  <div v-else>
    <div id="app" style="height: 600%;">
      <FundsTable
        v-bind:funds="funds"
        v-bind:fund_category="fund_category"
        v-bind:fund_type="fund_type"
        v-bind:plans="plans"
        v-bind:sortOrder="sortOrder"
        v-on:sortBy="sortBy"
        v-on:sortByVolatility="sortByVolatility"
        v-on:sortByReturns="sortByReturns"
        v-on:find="find"
      />
    </div>
  </div>
</template>

<script>
import Sort from "./Sort.vue";
import FundsTable from "./FundsTable.vue";

import axios from "axios";

var _ = require("lodash/core");

const funds = require("../funds.json");
// console.log(funds.length);

function dynamicSort(property) {
  var sortOrder = 1;

  if (property[0] === "-") {
    sortOrder = -1;
    property = property.substr(1);
  }

  return function(a, b) {
    if (sortOrder == -1) {
      return b[property].localeCompare(a[property]);
    } else {
      return a[property].localeCompare(b[property]);
    }
  };
}

export default {
  name: "app",
  components: {
    Sort,
    FundsTable
  },
  data() {
    return {
      funds: [],
      // funds: funds.slice(0, 100),
      fund_category: [],
      fund_type: [],
      plans: [],
      sortOrder: "asc",
      loading: true
    };
  },
  methods: {
    sortBy(column) {
      if (this.sortOrder === "asc") {
        this.funds.sort(dynamicSort(column));
        this.sortOrder = "desc";
      } else {
        this.funds.sort(dynamicSort(`-${column}`));
        this.sortOrder = "asc";
      }
    },
    sortByVolatility() {
      if (this.sortOrder === "asc") {
        this.funds.sort((a, b) => {
          return a.volatility - b.volatility;
        });
        this.sortOrder = "desc";
      } else {
        this.funds.sort((a, b) => {
          return b.volatility - a.volatility;
        });
        this.sortOrder = "asc";
      }
    },
    sortByReturns(year) {
      // console.log("sortByReturns", year);
      if (this.sortOrder === "asc") {
        this.funds.sort((a, b) => {
          return a.returns[`${year}`] - b.returns[`${year}`];
        });
        this.sortOrder = "desc";
      } else {
        this.funds.sort((a, b) => {
          return b.returns[`${year}`] - a.returns[`${year}`];
        });
        this.sortOrder = "asc";
      }
    },
    find(column) {
      const col = column[0];
      const value = column[1];
      this.funds = funds.slice(0, 100).filter(val => {
        if (val[`${col}`] === value) {
          // console.log(val);
          return true;
        }
      });
    }
  },
  created() {
    axios
      .get("https://api.kuvera.in/api/v3/funds.json")
      .then(res => {
        const data = res.data;
        this.funds = data.slice(0, 100);
        var fund_category = new Set();
        var fund_type = new Set();
        var plans = new Set();
        data.map(val => {
          if (val.fund_category != undefined && val.fund_category != null) {
            fund_category.add(val.fund_category);
          }
          if (val.fund_type != undefined && val.fund_type != null) {
            fund_type.add(val.fund_type);
          }
          // console.log(val.fund_type);
          if (val.plan != undefined && val.plan != null) {
            plans.add(val.plan);
          }
        });
        this.fund_category = Array.from(fund_category);
        this.fund_type = Array.from(fund_type);
        this.plans = Array.from(plans);
        this.loading = false;
      })
      .catch(err => {
        this.loading = false;
        alert("some error occured");
        console.log(err);
      });
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
