<template>
  <div class="text-center" v-if="loading">
    <div class="spinner-border" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>
  <div v-else class="container">
    <div class="card">
      <div class="card-body">
        <h2 class="text-center">Explore Fund</h2>
        <p>
          <strong>Fund Name:</strong>
          <a target="_blank" :href="fund.detail_info">
          {{fund.fund_name}}
          </a>
        </p>
        <p>
          <strong>Name:</strong>
          {{fund.name}}
        </p>
        <p>
          <strong>Category:</strong>
          {{fund.category}}
        </p>
        <p>
          <strong>Investment Objective:</strong>
          {{fund.investment_objective}}
        </p>
        <p>
          <strong>Fund House:</strong>
          {{fund.fund_house}}
        </p>
        <p>
          <strong>Fund Category:</strong>
          {{fund.fund_category}}
        </p>
        <p>
          <strong>Plan:</strong>
          {{fund.plan}}
        </p>
        <p>
          <strong>Fund Manager:</strong>
          {{fund.fund_manager}}
        </p>
        <p>
          <strong>Crisil Rating:</strong>
          {{fund.crisil_rating}}
        </p>
        <p>
          <strong>Volatility:</strong>
          {{fund.volatility}}
        </p>
        <p>
          <strong>Returns:</strong>
          <table>
            <tr>
              <th>Year 1</th>
              <td>{{fund.returns.year_1}}</td>
            </tr>
            <tr>
              <th>Year 3</th>
              <td>{{fund.returns.year_3}}</td>
            </tr>
            <tr>
              <th>Year 5</th>
              <td>{{fund.returns.year_5}}</td>
            </tr>
            <tr>
              <th>Inception</th>
              <td>{{fund.returns.inception}}</td>
            </tr>
          </table>
        </p>
        <p>
          <strong>Can You Switch:</strong>
          {{fund.switch_allowed}}
        </p>
        <p>
          <strong>SIP Maximum Gap:</strong>
          {{fund.sip_maximum_gap}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ExploreFund",
  data() {
    return {
      fund: {},
      loading: true,
    };
  },
  created() {
    axios
      .get(`https://api.kuvera.in/api/v3/funds/${this.$route.params.code}.json`)
      .then(res => {
        this.fund = res.data[0];
        this.loading = false;
      })
      .catch(err => {
        this.loading = false;
        // alert("some error occured",err);
        console.log(err);
      });
  }
};
</script>

